import axios from "axios";
import config from "../config";
import _t from "./i18n.service";

class AuthService {

  login(email, password) {
    return new Promise((resolve, reject) => {
      axios.post(config.API_URL + "/login", {
        email: email,
        password: password
      })
      .then(res => {
        let user_data = {
          email: email,
          role_id: res.data.role_id,
          role: res.data.role_id === 1 ? 'ROLE_PROFESSOR' : 'ROLE_STUDENT',
          user_id: res.data.user_id,
          jwt: res.data.jwt,
        }; 
        localStorage.setItem('user', JSON.stringify(user_data));
        resolve(user_data);  
      })
      .catch(err => {
        reject({message: _t('login_failed')});
      });
    });
  }
  logout() {
    localStorage.removeItem("user");
  }

  register(email, password, role) {
    return new Promise((resolve, reject) => {
      axios.post(config.API_URL + "/user/create", {
        email: email,
        password: password,
        RoleId: role === 'ROLE_PROFESSOR' ? 1 : 2
      })
      .then(res => {
        resolve({message: _t('user_registered')});  
      })
      .catch(err => {
        let server_msg = '';
        if (err.response.data.message === 'Email address already exists.') {
          server_msg = _t('email_already_exists');
        }
        reject({message: _t('user_not_registered') + ' ' + server_msg});
      });
    });
  }

  getCurrentUser() {
    const user = JSON.parse(localStorage.getItem('user')) || {email: null};
    return user;
  }
}

export default new AuthService();