import React, { Component } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service";
import _t from "../../services/i18n.service";

const required = (value) => {
  if (!value || value.length === 0) {
    return (
      <div className="alert alert-danger" role="alert">
        {_t("field_required")}
      </div>
    );
  }
};

const posterior = (value) => {
  const now = new Date();
  const valueDate = new Date(value);
  if (valueDate <= now) {
    return (
      <div className="alert alert-danger" role="alert">
        {_t("deadline_posterior")}
      </div>
    );
  }
};

class NewQuestion extends Component {
  constructor(props) {
    super(props);

    this.handleCreate = this.handleCreate.bind(this);
    this.onSelectQuestion = this.onSelectQuestion.bind(this);
    this.onSetPrePost = this.onSetPrePost.bind(this);
    this.onSetDeadline = this.onSetDeadline.bind(this);
    this.showPrePost = this.showPrePost.bind(this);

    this.merit_time_options = window.$settings.merit_time_options;

    const first_available_question_type = props.question_types.find(qt => qt.prepost.length > 0);
    const id = first_available_question_type
    ? first_available_question_type.id
    : 0;
    const title = first_available_question_type
      ? first_available_question_type.question_options.title
      : "";
    const description = first_available_question_type
    ? first_available_question_type.question_options.description
    : "";
    const prepost = first_available_question_type
    ? first_available_question_type.prepost[0]
    : "";

    this.state = {
      id: id,
      title: title,
      description: description,
      deadline: null,
      prepost: prepost,
      merit_time: this.merit_time_options[
        Object.keys(this.merit_time_options)[0]
      ],
      showPre: this.showPrePost(title, "pre"),
      showPost: this.showPrePost(title, "post"),

      first_available_question_type: first_available_question_type,
      
      successful: false,
      message: "",
    };
  }

  showPrePost(title, prepost) {
    const show = this.props.question_types
    .find(qt => qt.question_options.title === title)
    .prepost.find(e => e === prepost);
    return show;
  }

  onSelectQuestion(e) {
    const question_type_id = parseInt(e.target.value);
    const question_options = this.props.question_types.find(
      (q) => q.id === question_type_id
    ).question_options;

    const new_state = {
      id: question_type_id,
      title: question_options.title,
      description: question_options.description,
      showPre: this.showPrePost(question_options.title, "pre"),
      showPost: this.showPrePost(question_options.title, "post"),
    };

    this.setState(new_state);

    console.log(question_type_id, new_state);
  }

  onSetDeadline(e) {
    this.setState({
      deadline: e.target.value,
    });
  }

  onSetMerit(e) {
    this.setState({
      merit_time: e.target.value,
    });
  }

  onSetPrePost(e) {
    this.setState({
      prepost: e.target.value,
    });
  }

  handleCreate(e) {
    e.preventDefault();
    this.setState({
      message: "",
      successful: false,
    });
    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      const answer_options = this.props.question_types.find(
        q => q.id === this.state.id
      ).answer_options;

      BackendService.newQuestion({
        // question info
        title: this.state.title,
        description: this.state.description,
        phase: this.props.classroom.phase,
        pre_post: this.state.prepost,
        language: window.$lang,
        ClassroomCode: this.props.classroom.code,
        deadline: this.state.deadline,
        reminder: this.props.classroom.reminder,
        merit_time: this.state.merit_time,
        options: answer_options,
      }).then(
        (response) => {
          this.setState({
            successful: true,
          });
          this.props.onNewQuestion(response.question);
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          if (
            error.response &&
            error.response.status &&
            error.response.status === 401
          ) {
            // token has expired
            this.setState({
              successful: false,
              message: _t("session_expired"),
            });
            setTimeout(this.props.logOut, 2000);
          } else {
            this.setState({
              successful: false,
              message: resMessage,
            });
          }
        }
      );
    }
  }

  render() {
    
    return (
      <div>
        <div className="center-form">
          <div className="">
            <h5>{_t("new_question_title")}</h5>
            {!this.state.first_available_question_type && <p>{_t('no_question_types')}</p>}
            {this.state.first_available_question_type && 
            <Form
              onSubmit={this.handleCreate}
              ref={(c) => {
                this.form = c;
              }}
            >
              {!this.state.successful && (                
                <div>
                  <div className="form-group mt-2">
                    <div className="mb-3">
                      <label className="form-label">
                        {_t("choose_question")}:
                      </label>
                      <div className="form-check">
                        {this.props.question_types.map((q, i) => 
                          <div key={`outer_${i}`}>
                            {(this.showPrePost(q.question_options.title, "pre") || this.showPrePost(q.question_options.title, "post")) &&
                              (<div className="p-1" key={`div_${i}`}>
                              <input
                                key={i}
                                type="radio"
                                id={q.id}
                                name="title"
                                className="form-check-input"
                                value={q.id}
                                onChange={this.onSelectQuestion}
                                validations={[required]}
                                defaultChecked={q.id === this.state.first_available_question_type.id}
                              />
                              <label className="form-check-label" key={`label_${i}`}>
                                {" "}
                                {q.question_options.title}
                              </label>
                            </div>)}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="form-group mt-2 mb-3">
                    <label
                      htmlFor="description"
                      className="form-check-label mb-1"
                    >
                      {_t("description")}:
                    </label>
                    <p className="text-secondary">
                      <i>{this.state.description}</i>
                    </p>
                  </div>

                  <div className="form-group mt-2 mb-3">
                    <label htmlFor="deadline" className="form-check-label mb-2">
                      {_t("deadline")}:
                    </label>
                    <Input
                      type="date"
                      name="deadline"
                      onChange={this.onSetDeadline}
                      validations={[required, posterior]}
                    />
                  </div>

                  <div className="form-group mt-2 mb-3">
                  {this.state.showPre &&                    
                    <div className="form-check">
                      <input
                        type="radio"
                        id="pre"
                        name="prepost"
                        value="pre"
                        className="form-check-input"
                        defaultChecked
                        onChange={this.onSetPrePost}
                      />
                      <label htmlFor="pre" className="form-check-label">
                        Pre
                      </label>
                    </div>
                    }
                    {this.state.showPost &&
                    <div className="form-check">
                      <input
                        type="radio"
                        id="post"
                        name="prepost"
                        value="post"
                        className="form-check-input"
                        onChange={this.onSetPrePost}
                      />
                      <label htmlFor="post" className="form-check-label">
                        Post
                      </label>
                    </div>
                    } 
                  </div>

                  <div className="form-group mt-2">
                    <label className="form-label">{_t("medit_time")}:</label>
                    <div className="col-12 text-center">
                      <Select
                        name="merit_time"
                        value="none"
                        className="form-select"
                        validations={[required]}
                        onChange={this.onSetMeritTime}
                      >
                        {
                          //<option value="0">{_t("none")}</option>
                        }
                        {Object.keys(this.merit_time_options).map((k, i) => (
                          <option key={`option_${i}`} value={k} name="merit_time">
                            {this.merit_time_options[k]}
                          </option>
                        ))}
                      </Select>
                    </div>
                  </div>

                  {this.state.message && (
                    <div className="form-group">
                      <div
                        className={
                          this.state.successful
                            ? "alert alert-success"
                            : "alert alert-danger"
                        }
                        role="alert"
                      >
                        {this.state.message}
                      </div>
                    </div>
                  )}

                  <div className="form-group mt-5 row">
                    <div className="col-6 text-center">
                      <button
                        className="btn btn-secondary btn-block"
                        onClick={this.props.onCancelNewQuestion}
                      >
                        {_t("cancel")}
                      </button>
                    </div>
                    <div className="col-6 text-center">
                      <button
                        className="btn btn-success btn-block"
                        onClick={this.handleCreate}
                      >
                        {_t("create")}
                      </button>
                    </div>
                  </div>
                </div>
              )}

              <CheckButton
                style={{ display: "none" }}
                ref={(c) => {
                  this.checkBtn = c;
                }}
              />
            </Form>}
          </div>
        </div>
      </div>
    );
  }
}

export default NewQuestion;
